.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onDektop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;

    margin-bottom: 20px;

    @include onDektop {
      margin-bottom: 32px;
    }
  }

  &__photo {
    width: 100%;
    border-radius: 50%;
    grid-column: 2 / 4;

    &:hover {
      transform: scale(1.2);
      transition: 0.4s;
    }
  }

  &__name {
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-align: center;

    margin: 0 0 4px;
  }

  &__role {
    margin: 0 0 4px;

    font-weight: 400;
    font-size: 14px;
    line-height: 27px;
    text-align: center;
    color: #9fadc2;
  }

  &__about {
    text-align: center;
  }
}

.link {
  color: #0075ff;
  text-decoration: none;
}
